.about-view {

}

.about-view-group {
    border-bottom: 1px solid #02d0c0;
    padding: 0 0 15px 0;
    margin: 15px;
}

.about-view .ender {
    border: none;
    padding: 0 0 15px 0;
    margin: 15px;
}

.about-view-group .group-title {
    font-size: 20px;
}

.about-view-group .item-value {
    padding-left: 30px;
}

.about-view-buttons button{
    border-radius: 10px;
}

.about-view-update-button {
    padding: 5px 5px;
    margin-top: 10px;
    border-radius: 10px;
    background-color: #e1f8d7;
}

.about-view-update-button button{
    float: right;
    border: 1px solid #02d0c0;
    padding: 2px 4px;
    border-radius: 7px;
}

.about-view-update-button button:hover, .about-view-update-button button:focus{
    border: 1px solid #02d0c0;
}

.about-view-update-button button:focus{
    border: 1px dashed #02d0c0;
}

.about-view-update-button button:active {
    border: 1px solid #02d0c0;
}

.about-view-update-button i{
    margin-right: 4px;
}
