.radio-switch {
    margin-top: 10px;
}

.radio-switch-item{
    margin: 0;
    padding: 0;
    color: #466df7;
}

.radio-switch-item:hover {
    color: #5376f9;
}

.radio-switch-item-name {
    margin-top: 6px;
    margin-left: 5px;
    float: left;
}

.radio-switch-outer-circle {
    padding: 0;
    width: 30px;
    height: 30px;
    border: 2px solid #647dfd;
    border-radius: 15px;
    float: left;
}

.radio-switch-inner-circle {
    margin: 6px;
    width: 14px;
    height: 14px;
    border-radius: 7px;
    background-color: transparent;
}

.checked {
    background-color: #647dfd;
}
