.main-menu {
    margin: 15px -15px;

}

.main-menu li a {
    background-color:  #4b68f9;
    color: white;
}


.main-menu li i {
    float:right;
}

.main-menu li a:hover {
    background-color:  #9fadf8;
    color: #4b68f9;
}

.main-menu li a:focus {
    background-color:  #9fadf8;
    color: #4b68f9;
}

.main-menu li .selected {
    background-color: #7b8ff8;
}

.main-menu .menu-item {
    padding-right: 10px;
}

.main-menu .fa {
    position: absolute;
    top: 14px;
    right: 10px;
    font-size: 15px;
}

.main-menu-mode-toggle {
    padding-top: 15px;
    padding-bottom: 5px;
    border-bottom: 1px solid #4b68f9;
    border-left: 1px solid #4b68f9;
    border-right: 1px solid #4b68f9;
    text-align: center;
}
